import Footer from "@/components/footer";
import Sidebar from "@/components/sidebar";
import { useProtectedRoutesApi } from "@/libraries/API/ProtectedRoute/secureRoutes";
import { useQuery } from "@tanstack/react-query";

export default function Home() {
  const { GetInfantList } = useProtectedRoutesApi();

  const {
    data: percentage,
    isLoading: isPercentageLoading,
    isError: isPercentageError,
    error: percentageError,
  } = useQuery({
    queryKey: ["percentage"],
    queryFn: GetInfantList,
  });

  if (isPercentageLoading) {
    return (
      <div>
        <p>Loading...</p>
      </div>
    );
  }

  if (isPercentageError) {
    return (
      <div>
        <p>Error: {percentageError.message}</p>
      </div>
    );
  }

  const infants = percentage;

  return (
    <div className="grid grid-cols-[250px_1fr] grid-rows-[1fr_auto] min-h-screen">
      {/* Sidebar */}
      <Sidebar />

      {/* Main Content */}
      <main className="p-8 sm:p-20 font-[family-name:var(--font-geist-sans)] flex flex-col gap-8">
        <h1 className="text-2xl font-bold">Hello World</h1>
        <p>Welcome to our page!</p>
        <p>{JSON.stringify(infants, null, 2)}</p>
      </main>

      {/* Sticky Footer */}
      <Footer />
    </div>
  );
}
